<template>
  <div class="topHeader">
    <mt-header :title="title" fixed style="backgroundColor:'#00487C'">
      <router-link :to="geturl()" :replace="replace == 'true'" slot="left">
        <i class="mintui mintui-back" @click="jump()" v-if="show==1"></i>
      </router-link>
      <template v-if="emptyRight">
        <div class="mint-header-button"></div>
      </template>
      <template v-else>
        <mt-button class="prints" slot="right" v-if="rightTitle!=''">{{rightTitle}}</mt-button>
        <mt-button icon="more" v-show="show==1" @click="sheetVisible=true" slot="right" v-else></mt-button>
      </template>
    </mt-header>
    <actionsheet :actions="actions" v-model="sheetVisible"></actionsheet>
  </div>
</template>

<script>
import { Actionsheet, MessageBox } from "mint-ui";
export default {
  props: {
    show: {
      default: 1
    },
    back: {
      default: true
    },
    emptyRight: {
      default: false
    },
    rightTitle: {
      default: ""
    },
    title: {
      default: ""
    },
    url: {
      default: ""
    },
    replace: {
      default: "true"
    }
  },
  data() {
    return {
      sheetVisible: false,
      actions: []
    };
  },

  mounted() {
    // document.title = this.title;
    this.actions = [
      // {
      //     name: "刷新",
      //     method: this.refresh
      // },
      {
        name: "云餐饮",
        method: this.gohome
      },
      {
        name: "Simphony",
        method: this.goSimphony
      }
    ];
  },
  methods: {
    jump() {
      this.$emit("notseeChild");
    },
    geturl() {
      if (this.$route.query.redirect != null) {
        return this.$route.query.redirect;
      }
      return this.url;
    },
    gohome() {
      this.$router.push("/");
    },
    goSimphony() {
      MessageBox.confirm("是否切换Simphony", "")
        .then(action => {
          localStorage.setItem("system", 3);
          this.goSystem();
        })
        .catch(action => {});
    },
    goSystem() {
      var s = localStorage.system;
      if (s == "1") {
        if (localStorage.isLogin == "true") {
          this.$router.push("/");
        } else {
          this.$router.push("/loginCloudDining");
        }
      } else if (s == "2") {
        this.$router.push("/anypay");
      } else if (s == "3") {
        this.$router.push("/operapay");
      } else if (s == "4") {
        this.$router.push("/checkIndex");
      } else if (s == "5") {
        this.$router.push("/foodLogin");
      }
      localStorage.isLogin = "false";
    },
    refresh() {
      location.reload();
    }
  },
  components: {
    actionsheet: Actionsheet
  }
};
</script>

<style lang="less" scoped>
.topHeader {
  height: 1.04rem;
  background: #00487c;
  .mint-header {
    color: #fff;
    z-index: 100;
    background: #00487c !important;
    .mint-button {
      vertical-align: top;
    }
  }
  .is-left a {
    color: #fff;
    padding: 0.2rem 0.6rem 0.2rem 0;
  }
  .mint-header-button > a {
    text-decoration: none;
  }
  .prints {
    margin-right: 10px;
  }
}
</style>



