  <template>
  <div class="aettingApiBox">
    <topNavCloudDining title="参数设置" url="/"></topNavCloudDining>
    <div class="titleS">请选择云餐饮接口地址</div>
    <div class="settingContent">
      <table>
        <tr>
          <th style="width:8%;"></th>
          <th style="width:23.3%">网络</th>
          <th>地址</th>
          <th style="width: 8.5%;">响应</th>
        </tr>
        <tr v-for="item in address" :key="item.url">
          <td style="border:none">
            <div class="checkBox">
              <input
                type="radio"
                :value="item.url"
                name="urlcheck"
                v-model="vurl"
                :checked="select != null && select.url == item.url"
              >
            </div>
          </td>
          <td>{{item.name}}</td>
          <td>{{item.url}}</td>
          <td>{{item.ping}}</td>
        </tr>
      </table>
      <div class="settingAll">
        <div class="allBox">
          <div class="testButton" @click="ping(0)">测试</div>
          <router-link to="addApiCloudDining">
            <div class="testButton">新增</div>
          </router-link>
          <div class="testButton" @click="selectUrl">选择</div>
          <div class="testButton" @click="clear">清空接口</div>
        </div>
      </div>
    </div>
    <!-- <div class="defaultNumber">
      <h3 v-if="false">默认结账工号</h3>
      <div v-if="false" class="inputNumber">
        <input type="text" class="workNumber" v-model="ps.accountLogUser" @blur="saveParams">
      </div>
      <div class="switch clearfix" style="margin-left:3.65%;margin-bottom:0.3rem">
        <label>电子发票</label>
        <mt-switch v-model="ps.invoice" @change="saveParams"></mt-switch>
      </div>
      <template v-if="ps.isCoupon">
        <h3>卡券核销营业点</h3>
        <div
          style="float: right;margin-right: 0.5rem;height: 0.5rem;line-height: 0.8rem;"
          @click="clearpccode"
        >[清空]</div>
        <div class="inputNumber">
          <gpicker ref="pccode" @change="changeP()" placeholder="空全酒店核销"></gpicker>
        </div>
      </template>
      <template v-else>
        <div class="switch clearfix" style="margin-left:3.65%;margin-bottom:0.3rem">
          <label>卡券核销</label>
          <mt-switch v-model="ps.isCoupon" @change="getparams();saveParams();"></mt-switch>
        </div>
      </template>
      <div>
        <div style="float:left;width:50%">
          <h3>支付时打印份数</h3>
          <div class="inputNumber">
            <input
              type="text"
              class="workNumber"
              placeholder="默认只打印1份"
              v-model="ps.printPayCount"
              @blur="saveParams"
            >
          </div>
        </div>
        <div style="float:left;width:50%">
          <h3>转房/AR账打印份数</h3>
          <div class="inputNumber">
            <input
              type="text"
              class="workNumber"
              placeholder="默认只打印1份"
              v-model="ps.printRoomCount"
              @blur="saveParams"
            >
          </div>
        </div>
      </div>
    </div>-->
  </div>
</template>

<script>
import { Toast } from "mint-ui";
import axios from "axios";
import gpicker from "../common/gpicker";
import topNavCloudDining from "../common/topNavCloudDining";
export default {
  data() {
    return {
      address: [],
      select: null,
      vurl: "",
      ps: !!localStorage.ps
        ? JSON.parse(localStorage.ps)
        : {
            accountLogUser: "",
            invoice: false,
            pccode: null,
            pcname: null,
            isCoupon: false,
            printPayCount: 1,
            printRoomCount: 1
          }
    };
  },
  mounted() {
    var selectJson = localStorage.getItem("gateway");
    if (selectJson != null) {
      this.select = JSON.parse(selectJson);
      this.vurl = this.select.url;
    }
    try {
      var adds = localStorage.getItem("address");
      //console.log(selectJson);
      // console.log(adds);
      if (adds != null) {
        var urls = adds.split("|");
        for (var i = 0; i < urls.length; i++) {
          var data = JSON.parse(urls[i]);
          data.ping = "";
          this.address.push(data);
        }
        this.ping();
      }
    } catch (ex) {}

    if (this.ps.isCoupon) {
      //this.getparams();
    }
  },
  methods: {
    clear() {
      localStorage.removeItem("gateway");
      localStorage.removeItem("address");
      this.address = [];
      this.ps.accountLogUser = "";
    },
    clearpccode() {
      this.$refs.pccode.clear();
      this.changeP();
    },
    changeP() {
      this.ps.pccode = this.$refs.pccode.getValue();
      this.ps.pcname = this.$refs.pccode.getDisValueF();
      this.saveParams();
    },
    getparams() {
      this.$http.post("/pos/posUnion/getPosPccode", {}).then(res => {
        if (res.data.errorCode == 0) {
          var psv = [];
          _.forEach(res.data.result, (el, key) => {
            psv.push({
              code: el.code,
              name: el.descript
            });
          });
          this.$refs.pccode.setData(psv, this.ps.pccode, this.disCodeName);
        } else {
          this.msg(res.data.errorMsg);
        }
      });
    },
    disCodeName(v) {
      return v.code + " -- " + v.name;
    },
    ping(i) {
      if (i == undefined) {
        i = 0;
      }
      var add = this.address;
      if (add.length > i) {
        var item = this.address[i];
        var requestTime = new Date().getTime();
        var instance = axios.create({
          timeout: 5000
        });
        instance
          .get(item.url + "/pos/posUnion/ping")
          .then(res => {
            var responseTime = new Date().getTime();
            var pv = Math.abs(requestTime - responseTime);
            item.ping = pv;
          })
          .catch(function(error) {
            item.ping = "超时";
          });
        this.ping(i + 1);
      }
    },
    saveParams() {
      this.ps.accountLogUser = this.ps.accountLogUser.toUpperCase();
      localStorage.setItem("ps", JSON.stringify(this.ps));
    },
    selectUrl() {
      for (var i = 0; i < this.address.length; i++) {
        if (this.address[i].url == this.vurl) {
          localStorage.setItem("gateway", JSON.stringify(this.address[i]));
          this.msg("选择成功");
          return;
        }
      }
      this.msg("没有选中节点");
    },
    switchFun() {},
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  components: {
    gpicker,
    topNavCloudDining
  }
};
</script>

<style lang="less">
.aettingApiBox {
  width: 100%;
  margin: 0 auto;
  text-align: left;

  .titleS {
    width: 96%;
    margin: 0 auto;
    font-size: 0.32rem;
    display: block;
    height: 1.0667rem;
    color: #666666;
    line-height: 1.0667rem;
  }

  .settingContent {
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    width: 96%;
    margin: 0 auto;
    height: auto;
    background: #fff;

    table {
      margin: 0 auto;
      font-size: 0.3733rem;
      width: 98%;

      tr {
        th {
          height: 1.36rem;
          border-bottom: 0.0267rem solid #eeeeee;
        }
      }

      tr {
        td {
          height: 1.0667rem;
          border-bottom: 0.0267rem dashed #eeeeee;

          .checkBox {
            width: 0.4267rem;
            height: 0.4267rem;
            margin: 0 auto;
            border-radius: 0.1333rem;

            .icon-right {
              color: #6ed200;
              line-height: 0.4267rem;
            }
          }
        }
      }
    }

    .settingAll {
      width: 100%;
      height: auto;

      .allBox {
        width: 85%;
        margin: 0 auto;
        margin-top: 0.5333rem;
        padding-bottom: 0.32rem;

        .testButton {
          width: 44%;
          height: 0.9867rem;
          text-align: center;
          line-height: 0.9067rem;
          color: #fff;
          font-size: 0.3733rem;
          background: #ff8902;
          border-radius: 0.1867rem;
          display: inline-block;
          margin-bottom: 0.2667rem;
          margin-left: 0.36rem;
        }
      }
    }
  }

  .defaultNumber {
    box-shadow: 0 0 0.2133rem 0rem #dadada;
    border-radius: 0.1333rem;
    width: 96%;
    margin: 0 auto;
    overflow: hidden;
    margin-top: 0.2667rem;
    background: #fff;
    padding-top: 0.0267rem;

    h3 {
      font-size: 0.32rem;
      color: #666666;
      margin-top: 0.3733rem;
      margin-bottom: 0.1333rem;
      margin-left: 3.65%;
    }

    .inputNumber {
      width: 92.7%;
      margin: 0 auto;
      padding-bottom: 0.5333rem;

      .workNumber {
        width: 90%;
        padding-left: 0.2667rem;
        border-radius: 0.1333rem;
        height: 0.9867rem;
        text-transform: uppercase;
        border: 0.0267rem solid #cccccc;
        margin: 0 auto;
      }

      .gpicker {
        position: relative;
        margin: 0 0.13333333rem;
        padding: 0 0.13333333rem;
        float: left;
        width: 7rem;
        box-sizing: border-box;
        height: 0.88rem;
        background: #fff;
        border: 1px solid #e8e8e8;
        border-radius: 2px;

        .ginput {
          border: none;
          width: 100%;
          height: 0.82rem;
          box-sizing: border-box;

          border-radius: 2px;
          border-radius: 2px;
        }

        .iconfont {
          position: absolute;
          right: 0.26666667rem;
          top: 0px;
          /* transform: rotate(-90deg);*/
        }
      }
    }

    .mint-switch-input:checked + .mint-switch-core {
      border-color: #ff8902;
      background-color: #ff8902;
    }
  }
}
</style>



